/*Typography
--------------------------------------------------------------*/
body,
button,
input,
select,
textarea {
	color: #404040;
	font-family: 'Comfortaa', Arial, sans-serif;
	font-size: 14px;
  line-height: 1.5;
  text-align: center;
  background-color: white;
}

h1,h2,h3,h4,h5,h6 {
  letter-spacing: 2px;
}

p {
  margin-bottom: 1.5em;
  color: #ffffff;
}

strong {
  font-weight: bold;
}

address {
  margin: 0 0 1.5em;
}

article {
  margin: 15px;
  padding: 15px;
}

img {
  display: block;
  border: 0;
  width: 25%;
  height: auto;
  margin-left: auto;
  margin-right: auto;
}

img#smallicon {
  width: 75px;
  height: 75px;
}

.card {
  word-wrap: normal;
  margin-left: auto;
  margin-right: auto;
}

.section-content {
  padding: 1.4em;
}

.container-cards {

  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  max-width: 1200px;
  margin: auto;
}

#AdditionalInfo {
  display: flex;
  align-content: space-around;
  padding: 15px;
 
}
small {
  font-size: 75%;
}

header {
  margin-top: none;
  padding-top: none;
  background-color:none;
  border: none;
}

.site-title {
  color: rgb(255, 255, 255);
  text-align: center;
  margin: 0;
  flex-grow: 2;
  padding-top: 10px;
  padding-right: 150px;
}

.flex-item {
  flex-basis: 100%;
}

nav .container {
  display: flex; 
  margin-bottom: 0;
  /* padding-right: 150px; */
  /* align-items: flex-start; */
}

nav .container li {
  flex: 1 1;
  padding: 10px;
  text-align: center;
  font-size: 1.5em;
  color: rgb(255, 255, 255);
  box-sizing: border-box;
  background-color: none;
  list-style-type: none;
}
.navbar-toggler {
  text-align: right;
}
nav .container a {
  color: rgb(255, 255, 255);
  text-transform: uppercase;
  font-size: 22px;
}

nav .container a:hover {
  color: orangered;
}
.flexContainer {
  display: flex;
  align-items: left;
  justify-content: center;
  background-color: rgba(255, 255, 255, 0.479);
  width: 100%;
}

.flexContainer2 {
  display: flex;
  align-items: left;
  justify-content: center;
  flex-wrap: wrap;
  
}

#classLogo {
  height: 90px;
  width: 90px;
}



@media only screen and (max-width: 800px) {
  
  nav .container {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  #classLogo {
    height: 31px;
    width: 31px;
  }
  .navbar-fixed-top {
    position: fixed;
    z-index: 100;
    background-color: rgba(255, 255, 255, 0.705);
    width: 100%;
  }
  #center-nav-elements {
    align-items: start;
  }
}
.navbar-fixed-top {
  position: fixed;
  z-index: 100;
  background-color: rgba(255, 255, 255, 0);
  width: 100%;
}
.bg-nav {
  background-color: white;
  transition: background-color 0.5s ease;
}

.bg-nav .nav-a {
  color:  rgb(112, 112, 112);

}

#rsvpButton {
